import {Directive, HostListener, Input} from '@angular/core';
import {AdaptTableComponent} from '../../adapt-table.component';
import {RowDataItem} from '../../adapt-table.models';

@Directive({
  // eslint-disable-next-line @angular-eslint/directive-selector
  selector: '[pRowToggler]'
})
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export class RowToggler {

  @Input('pRowToggler') data: RowDataItem;

  @Input() pRowTogglerDisabled: boolean;

  constructor(public dt: AdaptTableComponent) {
  }

  @HostListener('click', ['$event'])
  onClick(event: Event): void {
    if (this.isEnabled()) {
      this.dt.toggleRow(this.data, event);
      event.preventDefault();
    }
  }

  isEnabled(): boolean {
    return this.pRowTogglerDisabled !== true;
  }
}
